
html{
  height: 100%;
  width: 100%;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  position: absolute; 
  height: 300px;
  width: 400px;
  top: 50%; 
  left: 45%;
  transform: translate(-50%,-50%);
}

.game-info {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.game-board{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
}

